<template>
  <div class="home">
    <van-nav-bar title="首页" />
    <van-search
      placeholder="请输入搜索关键词"
      show-action
      shape="round"
      @focus=" particularsGo({name:'search'})"
    >
      <div slot="action">搜索</div>
    </van-search>
    <div class="home-box">
      <div class="banner-box">
        <div class="banner">
          <van-swipe :autoplay="3000" indicator-color="white">
            <van-swipe-item>
              <img
                class="auto-img"
                src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1576145049762&di=3044bff134c3f74f41861cbc0a2feaf5&imgtype=0&src=http%3A%2F%2Fpic1.win4000.com%2Fwallpaper%2F4%2F543c9a8d7cdfe.jpg"
                alt
              />
            </van-swipe-item>
            <van-swipe-item>
              <img
                class="auto-img"
                src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1576145459619&di=24f1e900198e853f13780e981bf2d733&imgtype=0&src=http%3A%2F%2Fpic.3h3.com%2Fup%2F2016-11%2F20161125163120986080.jpg"
                alt
              />
            </van-swipe-item>
            <van-swipe-item>
              <img
                class="auto-img"
                src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1576145617634&di=1621d83d10a04d3c5f93ce9183d1847c&imgtype=0&src=http%3A%2F%2Fi0.hdslb.com%2Fbfs%2Farchive%2Fc2a3dc1eef03f3911aee4099850549916f2567d2.jpg"
                alt
              />
            </van-swipe-item>
            <van-swipe-item>
              <img
                class="auto-img"
                src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1576146542283&di=78e85f0c37210009a971c13017436721&imgtype=jpg&src=http%3A%2F%2Fimg3.imgtn.bdimg.com%2Fit%2Fu%3D3231604888%2C2439860271%26fm%3D214%26gp%3D0.jpg"
                alt
              />
            </van-swipe-item>
          </van-swipe>
        </div>
      </div>
      <div class="tank">
        <div class="cartoon">
          <div class="cartoon-t clearfix">
            <div class="ls fl"></div>
            <span class="fl title">最新漫画</span>
          </div>
          <div class="clearfix cartoonB">
            <div
              class="cartoonbox fl"
              v-for="(item,index) in cartoonboxD.data"
              :key="index"
              @click="particularsGo({name: 'particulars', params: {url: item.url}})"
            >
              <div class="cartoonitem">
                <div class="pho">
                  <van-image class="auto-img" :src="item.cover" alt>
                    <template v-slot:error>
                      <img class="auto-img" src="../../assets/image/404.jpg" alt />
                    </template>
                  </van-image>
                </div>
                <div class="texts">
                  <p class="name one-text">{{item.name}}</p>
                  <p class="infor one-text">{{item.tag}}</p>
                </div>
              </div>
            </div>
          </div>
          <div class="more" @click="particularsGo({name: 'videos'})">查看更多</div>
        </div>
      </div>
      <div class="tank">
        <div class="fiction">
          <div class="fiction-t clearfix">
            <div class="ls fl"></div>
            <span class="fl title">小说</span>
          </div>
          <div class="clearfix fictionB">
            <div
              class="fictionbox fl"
              v-for="(item,index) in fictionboxD.data"
              :key="index"
              @click="particularsGo({name: 'details', params: {url: item.url}})"
            >
              <div class="fictionitem">
                <div class="pho">
                  <van-image class="auto-img" :src="item.cover" alt>
                    <template v-slot:error>
                      <img class="auto-img" src="../../assets/image/404.jpg" alt />
                    </template>
                  </van-image>
                </div>
                <div class="texts">
                  <p class="name one-text">{{item.name}}</p>
                  <p class="infor one-text">{{item.author}}</p>
                </div>
              </div>
            </div>
          </div>
          <div class="more" @click="particularsGo({name: 'fiction'})">查看更多</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";

const { mapState } = createNamespacedHelpers("homeModule");

export default {
  name: "home",

  computed: {
    ...mapState(["baseUrl", "cartoonboxD", "fictionboxD", "homeData"])
  },

  created() {
    this.axios({
      methods: "GET",
      url: "http://api.pingcc.cn/?mhname=1"
    })
      .then(result => {
        // console.log("result ==>> ", result);
        this.$store.commit("homeModule/cartoonboxs", result);
      })
      .catch(err => {
        // console.log("err ==>> ", err);
        this.$toast.clear();
      });

    this.axios({
      methods: "GET",
      url: "http://api.pingcc.cn/?xsname=1"
    })
      .then(results => {
        // console.log("results xslist ==>> ", results);
        this.$store.commit("homeModule/fictionboxs", results);
      })
      .catch(err => {
        // console.log("err ==>> ", err);
        this.$toast.clear();
      });
  },

  methods: {
    particularsGo(o) {
      this.$router.push(o);
    }
  }
};
</script>
<style >
.home .van-nav-bar__title {
  font-size: 18px;
  color: #e4393c;
  font-weight: bolder;
}
</style>
<style lang="less" scoped>
.home .van-nav-bar {
  position: fixed;
  top: 0px;
  bottom: 0px;
  left: 0;
  right: 0;
}
.home .van-search--show-action {
  position: fixed;
  top: 45px;
  right: 0;
  left: 0;
}

.home-box {
  position: fixed;
  top: 92px;
  bottom: 46px;
  left: 0;
  right: 0;
  overflow-y: auto;
}

.banner-box {
  background-color: #fff;
  padding: 10px;
}
.banner {
  height: 200px;
  border-radius: 10px;
  overflow: hidden;
}
.tank {
  background-color: #fff;
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 10px;
}
.cartoon {
  .cartoon-t {
    font-size: 20px;
    color: #e4393c;
    font-weight: bold;
    border-bottom: 1px solid #9999;
    .ls {
      height: 20px;
      border-left: 2px solid #e4393c;
      margin-top: 5px;
    }
    .title {
      margin: 0px 10px 5px;
    }
  }
  .cartoonB {
    margin-top: 8px;
  }
  .cartoonbox {
    width: calc(~"( 100% - 20px)  / 3 ");
    margin-right: 10px;
    margin-bottom: 5px;
    &:nth-child(3n) {
      margin-right: 0;
    }
    .cartoonitem {
      width: 100%;
      .pho {
        width: 100%;
        height: 153px;
      }
      .texts {
        width: 100%;
        .name {
          color: #e4393c;
          font-weight: bold;
          text-align: center;
          font-size: 16px;
          padding: 5px 0;
        }
        .infor {
          text-align: center;
          font-size: 16px;
        }
      }
    }
  }
  .more {
    text-align: center;
    margin-top: 10px;
    padding-top: 10px;
    border-top: 1px solid #9999;
    color: #e4393c;
    font-weight: bold;
    text-align: center;
    font-size: 20px;
  }
}

.fiction {
  .fiction-t {
    font-size: 20px;
    color: #e4393c;
    font-weight: bold;
    border-bottom: 1px solid #9999;
    .ls {
      height: 20px;
      border-left: 2px solid #e4393c;
      margin-top: 5px;
    }
    .title {
      margin: 0px 10px 5px;
    }
  }
  .fiction {
    margin-top: 8px;
  }
  .fictionbox {
    width: calc(~"( 100% - 20px)  / 3 ");
    margin-right: 10px;
    margin-bottom: 5px;
    &:nth-child(3n) {
      margin-right: 0;
    }
    .fictionitem {
      width: 100%;
      .pho {
        width: 100%;
        height: 153px;
        position: relative;
      }
      .texts {
        width: 100%;
        .name {
          color: #e4393c;
          font-weight: bold;
          text-align: center;
          font-size: 16px;
          padding: 5px 0;
        }
        .infor {
          text-align: center;
          font-size: 16px;
        }
      }
    }
  }
  .more {
    text-align: center;
    margin-top: 10px;
    padding-top: 10px;
    border-top: 1px solid #9999;
    color: #e4393c;
    font-weight: bold;
    text-align: center;
    font-size: 20px;
  }
}
</style>